.section.sentence-section {
  padding: calc(var(--index) * 3);
  background-image: url("sentences-bg.cb8e1aff.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  height: 45svh;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px #0006;
}

.sentence-section .title-wrapper {
  padding: var(--padding-bigger);
  backdrop-filter: blur(1px);
  background-color: #fffc;
  max-width: 75%;
  position: absolute;
  top: 30%;
  left: 0;
}

@media (width <= 600px) {
  .sentence-section .title-wrapper {
    padding: var(--padding-smaller);
  }
}

.sentence-section .title {
  color: var(--color-accent-darkest);
  text-wrap: balance;
  font-style: italic;
  font-size: var(--font-size-smallscreens-title);
  letter-spacing: .1rem;
}

.sentence-section .quotation-mark-box {
  top: calc(var(--index) * -3);
  position: absolute;
  left: 5%;
}

.sentence-section .quotation-mark {
  font-size: calc(var(--index) * 8);
  color: var(--color-accent-darker);
  font-family: Cambria, Cochin, Georgia, Times, Times New Roman, serif;
  font-style: italic;
}
